.songgrid{
  
  display: flex;
  grid-template-columns:  1fr 1fr ;
  background-color: #000102;
  font-size: .7em;
  width:85%;
position: absolute;
  grid-template-columns: 10em repeat(1fr, 1fr,1fr) 100px;
